.about {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #FFFFFF;
    padding: 80px 0;

    &__title {
        margin: 0 auto;
        text-align: center;
        letter-spacing: 2px;
        color: #1C1C1D;
        max-width: 700px;
        margin-bottom: 0.66666666666em; // 30px
    }

    &__text {
        margin: 0 auto;
        text-align: center;
        max-width: 501px;
        margin-bottom: 3.33333333333em; // 60px
    }
    min-width: 0;
}

.swiper {
    height: 300px;
    width: 95vw;
}

.swiper-pagination {
    top: 260px;
}

.swiper-pagination-bullet {
    width: 18px;
    height: 18px;
    border: 2px solid #1C1C1D;
    border-radius: 50%;
    margin-right: 15px;
    background-color: #fff;
    
    &:last-child {
        margin-right: 0;
    }
}

.swiper-pagination .swiper-pagination-bullet-active-next,
.swiper-pagination .swiper-pagination-bullet-active-next-next,
.swiper-pagination .swiper-pagination-bullet-active-prev,
.swiper-pagination .swiper-pagination-bullet-active-prev-prev {
    opacity: 1;
    transform: scale(1);
}
.swiper-pagination .swiper-pagination-bullet-active {
    background-color: #FFBD12;
} 

.slider {
    width: 100%;
    &__item {
        max-height: 183px;
        padding: 25px 20px 30px 20px;
        width: 340px;
        border: 2px solid #1C1C1D;
        box-shadow: 0px 4px 0px #202020;
        border-radius: 10px;

        &:nth-child(3n + 1) {
            background-color: #FFBD12;
        }
        
        &:nth-child(3n + 2) {
            background-color: #FA6648;
        }
        
        &:nth-child(3n) {
            background-color: #FFB0BC;
        }
    }
    &__item-top {
        display: flex;
    }

    // .slider__item-img

    &__item-img {
        object-fit: contain;
        max-width: 87px;
        max-height: 73px;
        width: 100%;
        margin-right: 22px;
    }

    // .slider__item-descr

    &__item-descr {
        font-size: 1.25rem; // 20px
        font-weight: bold;
        line-height: 120%;
        letter-spacing: 0.5px;
        color: #FFFFFF;

    }

    // .slider__item-text

    &__item-text {
        font-size: 1rem; // 16px
        line-height: 125%;
        margin-top: 15px;
    }
}

@media (max-width: 380px) {
    .slider {
        &__item {
            max-height: 183px;
            padding: 20px 15px 20px 15px;
            width: 300px;
        }

        &__item-img {
            object-fit: contain;
            max-width: 77px;
            width: 100%;
            margin-right: 16px;
        }
    
        // .slider__item-descr
    
        &__item-descr {
            font-size: 1.125rem; // 18px
            font-weight: bold;
            line-height: 120%;
            letter-spacing: 0.5px;
            color: #FFFFFF;
    
        }
    
        // .slider__item-text
    
        &__item-text {
            font-size: 1rem; // 16px
            line-height: 125%;
            margin-top: 15px;
        }
    }
}

@media (max-width: 340px) {
    .slider {
        &__item {
            max-height: 183px;
            padding: 15px 10px 15px 10px;
            width: 280px;
        }

        &__item-img {
            object-fit: contain;
            max-width: 77px;
            width: 100%;
            margin-right: 16px;
        }
    
        // .slider__item-descr
    
        &__item-descr {
            font-size: 1.125rem; // 18px
            font-weight: bold;
            line-height: 120%;
            letter-spacing: 0.5px;
            color: #FFFFFF;
    
        }
    
        // .slider__item-text
    
        &__item-text {
            font-size: 1rem; // 16px
            line-height: 125%;
            margin-top: 15px;
        }
    }
}


